<template>
  <div>
    <slot name="before"></slot>
    <div :id="elId"
      :style="style"></div>
    <slot name="after"></slot>
  </div>
</template>

<script>
// 引入基本模板
import echarts from 'echarts/lib/echarts'
import 'echarts/lib/chart/bar'
import 'echarts/lib/chart/line'
// 引入提示框组件、标题组件、工具箱组件。
import 'echarts/lib/component/legend'
import 'echarts/lib/component/tooltip'
import 'echarts/lib/component/title'
import 'echarts/lib/component/toolbox'

//引入uuid文件
import uuidv1 from 'uuid/v1'
export default {
  name: 'echartbar',
  props: {
    lineechartbar: {
      type: Object,
      default() {
        return {
          width: '100%',
          height: '300px',
          color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
            { offset: 0, color: '#ffffff' },
            { offset: 1, color: '#ecf4ff' }
          ]),
          // {
          //   type: [String, Object],
          //   default() {
          //     return new echarts.graphic.LinearGradient(0, 0, 0, 1, [
          //       { offset: 0, color: "#ffffff" },
          //       { offset: 1, color: "#ecf4ff" }
          //     ]);
          //   }
          // },
          title: ['总检医生工作量'],
          xAxisDate: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
          seriesData: [10, 52, 200, 334, 390, 330, 220]
        }
      }
    }
  },
  data() {
    return {
      elId: '',
      chartObj: {},
      option: {}
    }
  },
  created() {
    this.elId = uuidv1() //获取随机id
    console.log(this.lineechartbar)
  },
  computed: {
    style() {
      return {
        height: this.lineechartbar.height,
        width: this.lineechartbar.width
      }
    }
  },
  watch: {
    lineechartbar: {
      handler(val) {
        this.option.series[0].data = this.lineechartbar.seriesData
        this.option.xAxis[0].data = this.lineechartbar.xAxisDate
        this.chartObj = echarts.init(document.getElementById(this.elId))
        this.chartObj.setOption(this.option)
      },
      deep: true
    }
  },
  mounted() {
    const mytextStyle = {
      color: '#466791', //文字颜色
      fontStyle: 'normal', //italic斜体  oblique倾斜
      fontWeight: 'normal', //文字粗细bold   bolder   lighter  100 | 200 | 300 | 400...
      fontFamily: 'MicrosoftYaHei', //字体系列
      fontSize: 14 //字体大小
    }
    const mylineStyle = {
      color: '#000', //颜色，'rgb(128, 128, 128)'，'rgba(128, 128, 128, 0.5)'，支持线性渐变，径向渐变，纹理填充
      // shadowColor: "red", //阴影颜色
      type: 'dotted', //坐标轴线线的类型，solid，dashed，dotted
      width: 2, //坐标轴线线宽
      opacity: 0.06 //图形透明度。支持从 0 到 1 的数字，为 0 时不绘制该图形
    }
    const mainlineStyle = {
      width: 3,
      shadowColor: ' rgba(169, 207, 255, 0.8)',
      shadowBlur: 10,
      shadowOffsetY: 10
    }
    const itemLineStyle = {
      // 线条颜色
      normal: {
        color: new echarts.graphic.LinearGradient(0, 0, 1, 0, [
          { offset: 0, color: '#ffd964' },
          { offset: 1, color: '#fc965f' }
        ])
      }
    }
    const itemBarStyle = {
      // 柱状颜色
      normal: {
        barBorderRadius: 3,
        color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
          { offset: 0, color: '#ff5252' },
          { offset: 1, color: '#fbba81' }
        ])
      },
      emphasis: {
        // areaColor: "#ff0000",
        label: {
          color: '#466791'
        },
        color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
          { offset: 0, color: '#ff5252' },
          { offset: 1, color: '#fbba81' }
        ])
      }
    }
    const itemBarStyle2 = {
      // 柱状颜色
      normal: {
        barBorderRadius: 3,
        color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
          { offset: 0, color: '#00d0a6' },
          { offset: 1, color: '#46eabd' }
        ])
      },
      emphasis: {
        // areaColor: "#ff0000",
        label: {
          color: '#466791'
        },
        color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
          { offset: 0, color: '#00d0a6' },
          { offset: 1, color: '#46eabd' }
        ])
      }
    }

    const itemDataStyle = {
      normal: {
        label: {
          show: true, //开启显示
          position: 'top', //在上方显示
          formatter: '{c} %',
          textStyle: {
            //数值样式
            color: '#c3cdde',
            fontSize: 14
          }
        }
      }
    }
    Object.assign(itemBarStyle)
    const legendData = []
    legendData.push(this.title)
    this.option = {
      color: this.color,
      tooltip: {
        trigger: 'axis',
        axisPointer: {
          // 坐标轴指示器，坐标轴触发有效
          shadowStyle: {
            color: 'rgba(98,167,255,0)'
          },
          type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
        }
      },
      grid: {
        left: '3%',
        right: '4%',
        bottom: '3%',
        containLabel: true
      },
      legend: {
        // orient: "vertical",
        // y: "top",
        itemWidth: 10,
        itemHeight: 10
        // left: "auto",
        // top: "0",
        // right: "auto",
        // bottom: "auto",
        // textStyle: mytextStyle,
        // data: this.legendData
      },
      xAxis: [
        {
          type: 'category',
          data: this.lineechartbar.xAxisDate,
          axisLine: false,
          splitLine: false,
          axisLabel: {
            textStyle: mytextStyle
          }
          // axisTick: {
          //   alignWithLabel: true
          // }
        }
      ],
      yAxis: [
        {
          type: 'value',
          axisLine: false,
          splitLine: {
            //坐标轴在 grid 区域中的分隔线。
            lineStyle: mylineStyle
          },
          axisLabel: {
            textStyle: mytextStyle,
            formatter: '{value}'
          }
        }
      ],
      series: [
        // 可配置组合图表
        {
          name: this.lineechartbar.title[0],
          type: 'line',
          smooth: true,
          itemStyle: itemLineStyle,
          lineStyle: mainlineStyle,
          data: this.lineechartbar.seriesData
        }
      ]
    }
    this.chartObj = echarts.init(document.getElementById(this.elId))
    this.chartObj.setOption(this.option, true)
  }
}
</script>
<style scoped></style>
