<!--
 * @Descripttion: 报表
 * @Author: 曾元球
 * @Date: 2019-06-28 15:12:07
 * @LastEditors: 曾元球
 * @LastEditTime: 2019-08-08 15:14:55
 -->
<template>
  <div class="page trialpreson flex-v ReportForm">
    <el-breadcrumb separator-class="el-icon-arrow-right"
      class="headertitle">
      <el-breadcrumb-item class="titlecolor">报表分布图</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="tabledata padding20 "
      style="overflow:auto">
      <div class="countlist flex">
        <div class="countlist-item flex"
          v-for="(item, index) in countlist"
          :key="index"
          :style="{background:`url(${item.backImg})`}"
          @click="gonextpage(item.sexType, item.examStatus)">
          <div class="itemtitle">{{item.title}}</div>
          <div class="itemcount">{{item.count}}</div>
        </div>
      </div>
      <div class="flex-item flex-v">
        <div class="towtitle">总检医生工作量统计</div>
        <div class="echartconcent flex-item backechartconcent">
          <div style="height: auto;overflow: hidden;">
            <el-form ref="form"
              :model="form"
              size="small"
              label-width="80px"
              style="float:left">
              <el-form-item label="时间：">
                <el-select v-model="form.regionDate"
                  placeholder="请选择"
                  style="width:100px"
                  @change="changeselect">
                  <el-option label="日"
                    value="day"></el-option>
                  <el-option label="月"
                    value="month"></el-option>
                  <el-option label="季"
                    value="season"></el-option>
                  <el-option label="年"
                    value="year"></el-option>
                </el-select>
              </el-form-item>
            </el-form>
            <span style="float:right;cursor:pointer;color: #318bfe;font-size:14px"
              @click="totaldocpage">更多</span>
          </div>
          <div>
            <!-- <echartBar :xAxisDate="xAxisDate3" :seriesData="seriesData3" :title='toptitle' v-if="echartType"></echartBar> -->
            <echart-bar :lineechartbar="lineechartbarobj"
              v-if="echartType"></echart-bar>
          </div>
        </div>
        <div class="towtitle">复查人员统计</div>
        <div class="echartconcent flex-item flex">
          <div class="leftechartbinmain ">
            <div class="echartbintitle">
              <span>复查人员分析图</span>
              <span style="float:right;cursor:pointer;color: #318bfe;"
                @click="reviewerpage">更多</span>
            </div>
            <div style="height: auto;overflow: hidden;">
              <el-form ref="form2"
                :model="form2"
                size="small"
                label-width="60px"
                style="float:left;margin-top:10px;">
                <el-form-item label="时间：">
                  <el-select v-model="form2.regionDate"
                    placeholder="请选择"
                    style="width:100px"
                    @change="changeselectpie">
                    <el-option label="日"
                      value="day"></el-option>
                    <el-option label="月"
                      value="month"></el-option>
                    <el-option label="季"
                      value="season"></el-option>
                    <el-option label="年"
                      value="year"></el-option>

                  </el-select>
                </el-form-item>
              </el-form>
            </div>
            <div>
              <chart-pie :pieData="pieData"
                v-if="pieData.length" />
              <div v-else
                class="nodatapie">
                暂无数据
              </div>
            </div>
          </div>
          <div class="rightchartbinmain ">
            <div class="echartbintitle">
              <span>复查人员类型统计</span>
              <span style="float:right;cursor:pointer;color: #318bfe;"
                @click="reviewerpage">更多</span>
              <div class=" table-box"
                style="padding:10px 0">
                <!-- <el-carousel :interval="5000"
                  arrow="hover"
                  indicator-position="outside">
                  <el-carousel-item> -->
                <el-table stripe
                  ref="multipleTable"
                  :data="reviewTablelist"
                  tooltip-effect="dark"
                  height='300'
                  header-cell-class-name='custom-table-cell'>
                  <el-table-column type="index"
                    width="50"
                    label="序号">
                  </el-table-column>
                  <el-table-column align="center"
                    label="重点人群"
                    prop="renshu"
                    show-overflow-tooltip>
                    <template slot-scope="scope">
                      {{scope.row.personType | getBoxType}}
                    </template>
                  </el-table-column>
                  <el-table-column align="center"
                    prop="countPerson"
                    label="人数"
                    width="120">
                  </el-table-column>
                  <el-table-column align="center"
                    label="占比"
                    width="120">
                    <template slot-scope="scope">
                      <div>{{calcProportion(scope.row.countPerson)}}</div>
                    </template>
                  </el-table-column>
                </el-table>
                <!-- </el-carousel-item>
                </el-carousel> -->
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import EchartBar from '@/components/echart/echartbar'
import chartPie from '@/components/echart/chartPie'
import { formatDate, calcType } from '@/lib/utils'
export default {
  name: 'ReportForm',
  components: {
    EchartBar,
    chartPie
  },
  data() {
    return {
      oldtime: '',
      endtime: '',
      form: {
        regionDate: 'day'
      },
      form2: {
        regionDate: 'day'
      },
      echartType: false,
      xAxisDate3: [],
      seriesData3: [],
      lineechartbarobj: {
        xAxisDate: [],
        seriesData: [],
        title: [],
        height: '260px'
      },
      echartPieType: false,
      echartPienoData: false,
      pieData: [],
      toptitle: '总检医生工作量',
      countlist: [
        {
          title: '今日总体检数（人）',
          count: '',
          backImg: require('@/assets/img/topechartback1.png'),
          sexType: '',
          examStatus: ''
        },
        {
          title: '今日男性体检数（人）',
          count: '',
          backImg: require('@/assets/img/topechartback2.png'),
          sexType: '1',
          examStatus: ''
        },
        {
          title: '今日女性体检数（人）',
          count: '',
          backImg: require('@/assets/img/topechartback3.png'),
          sexType: '2',
          examStatus: ''
        },
        {
          title: '今日登记已体检数（人）',
          count: '',
          backImg: require('@/assets/img/topechartback4.png'),
          sexType: '',
          examStatus: 'succ'
        },
        {
          title: '今日登记未体检数（人）',
          count: '',
          backImg: require('@/assets/img/topechartback5.png'),
          sexType: '',
          examStatus: 'notSucc'
        }
      ],
      tableData: [{ name: '高血压', renshu: '2', zhanbi: '10%' }],
      reviewTablelist: []
    }
  },
  filters: {
    getBoxType: function(val) {
      let { nameList } = calcType(val)
      return nameList ? nameList.join('/') : '未分配'
    }
  },
  created() {
    this.chartstatistic()
    this.handleDate('date')
    this.totalcheckdoc()
    this.reviewpielist()
  },

  methods: {
    //算占比
    calcProportion(val) {
      let total = this.reviewTablelist.reduce((init, next) => {
        return init + 1 * next.countPerson
      }, 0)
      let count = (val / total) * 100
      return count === 0 ? 0 : `${count.toFixed(1)}%`
    },

    gonextpage(sexType, examStatus) {
      this.$router.push({
        name: 'medicalrecordDetil',
        query: {
          sexType: sexType,
          examStatus: examStatus
        }
      })
    },
    totaldocpage() {
      this.$router.push({
        name: 'totalcheckdoctor'
      })
    },
    reviewerpage() {
      this.$router.push({
        name: 'reviewerstatistics'
      })
    },
    //图表统计
    chartstatistic() {
      let url = '/charts/base/getOrgRegisterPersonsNum'
      const params = { createOrganizationId: '' }
      this.$post(url, params)
        .then(res => {
          this.countlist[0].count = res.data.allPersion
          this.countlist[1].count = res.data.malePersion
          this.countlist[2].count = res.data.femalePersion
          this.countlist[3].count = res.data.todayExamSuccPersion
          this.countlist[4].count = res.data.regNotExamPersion
        })
        .catch(error => {
          error && this.$message.error(error)
        })
    },
    //时间处理
    handleDate(value) {
      let date = new Date()
      let timestamp = new Date().getTime() //获取当前时间戳
      let yearsoldtime = formatDate(timestamp - 365 * 3 * 24 * 3600 * 1000) //三年前时间
      let montholdtime = formatDate(timestamp - 365 * 1 * 24 * 3600 * 1000) //一年前时间
      let threemontholdtime =
        date.getFullYear() +
        '-' +
        (date.getMonth() - 2 < 10 ? '0' + (date.getMonth() - 2) : date.getMonth() - 2) +
        '-' +
        (date.getDate() < 10 ? '0' + date.getDate() : date.getDate()) //三个月前时间
      let weeksoldtime = formatDate(timestamp - 6 * 24 * 60 * 60 * 1000) //一周前时间
      if (value === 'year') {
        this.oldtime = yearsoldtime
      } else if (value === 'month') {
        this.oldtime = montholdtime
      } else if (value === 'season') {
        this.oldtime = threemontholdtime
      } else {
        this.oldtime = weeksoldtime
      }
      this.endtime = formatDate(timestamp)
    },
    //日图表
    getformatData(e, nowTime, beforeMonth) {
      var oneDay = 60 * 60 * 24 * 1000
      var nowTime = nowTime + ' 00:00:00'
      var beforeMonth = beforeMonth + ' 00:00:00'
      var start = new Date(beforeMonth).getTime() - oneDay
      var end = new Date(nowTime).getTime() - oneDay
      var result = []
      for (var i = start; i <= end; i += oneDay) {
        result.push({ days: i + oneDay, dayworkNum: 0 })
      }
      result.forEach((element, index) => {
        element.days = formatDate(element.days)
        e.forEach(item => {
          if (element.days == item.days) {
            result.splice(index, 1, item)
          }
        })
      })
      return result
    },
    //月图表
    getmonthData(e) {
      var dataArr = []
      var data = new Date()
      var year = data.getFullYear()
      data.setMonth(data.getMonth() + 1, 1) //获取到当前月份,设置月份
      for (var i = 0; i < 12; i++) {
        data.setMonth(data.getMonth() - 1) //每次循环一次 月份值减1
        var m = data.getMonth() + 1
        m = m < 10 ? '0' + m : m
        dataArr.push({ months: data.getFullYear() + '-' + m, dayworkNum: 0 })
      }
      dataArr.forEach((element, index) => {
        e.forEach(item => {
          if (element.months == item.months) {
            dataArr.splice(index, 1, item)
          }
        })
      })
      dataArr.sort(function(a, b) {
        return Date.parse(a.months) - Date.parse(b.months) //时间正序
      })
      return dataArr
    },
    // 总检医生工作量统计
    totalcheckdoc() {
      let url = '/charts/base/getDoctorWorkload'
      const params = {
        createOrganizationId: '',
        startRegisterDate: this.oldtime,
        endRegisterDate: this.endtime,
        queryType: this.form.regionDate
      }
      this.$post(url, params)
        .then(res => {
          console.log('res', res.data)
          let t = this.getformatData(res.data, params.endRegisterDate, params.startRegisterDate)
          console.log(t)
          this.lineechartbarobj.xAxisDate = []
          this.lineechartbarobj.seriesData = []
          if (this.form.regionDate === 'day') {
            t.forEach(element => {
              this.lineechartbarobj.xAxisDate.push(element.days)
              this.lineechartbarobj.seriesData.push(element.dayworkNum)
            })
          } else if (this.form.regionDate === 'month') {
            let monthData = this.getmonthData(res.data)
            monthData.forEach(element => {
              this.lineechartbarobj.xAxisDate.push(element.months)
              this.lineechartbarobj.seriesData.push(element.dayworkNum)
            })
          } else if (this.form.regionDate === 'season') {
            let arry = [
              { season: '1', dayworkNum: 0 },
              { season: '2', dayworkNum: 0 },
              { season: '3', dayworkNum: 0 },
              { season: '4', dayworkNum: 0 }
            ]
            arry.forEach((element, index) => {
              res.data.forEach(item => {
                if (element.season == item.season) {
                  arry.splice(index, 1, item)
                }
              })
            })
            arry.forEach(element => {
              this.lineechartbarobj.xAxisDate.push(element.season)
              this.lineechartbarobj.seriesData.push(element.dayworkNum)
            })
          } else {
            let nowYear = new Date().getFullYear()
            let arry = []
            arry.push({ years: nowYear - 2, dayworkNum: 0 })
            arry.push({ years: nowYear - 1, dayworkNum: 0 })
            arry.push({ years: nowYear, dayworkNum: 0 })
            arry.forEach((element, index) => {
              res.data.forEach(item => {
                if (element.years == item.years) {
                  arry.splice(index, 1, item)
                }
              })
            })
            arry.forEach(element => {
              this.lineechartbarobj.xAxisDate.push(element.years)
              this.lineechartbarobj.seriesData.push(element.dayworkNum)
            })
          }
          this.echartType = true
          this.lineechartbarobj.title.push(this.toptitle)
        })
        .catch(error => {
          error && this.$message.error(error)
        })
    },
    //时间选择切换
    changeselect(e) {
      console.log(e)
      this.handleDate(e)
      this.totalcheckdoc()
    },
    //pie时间选择切换
    changeselectpie(e) {
      this.handleDate(e)
      this.reviewpielist()
    },
    // 复查人员统计
    reviewpielist() {
      let url = '/charts/base/getReviewUserType'
      const params = {
        createOrganizationId: '',
        startRegisterDate: this.oldtime,
        endRegisterDate: this.endtime
      }
      this.$post(url, params)
        .then(({ data }) => {
          this.reviewTablelist = data.list
          this.pieData = data.list.map((element, index) => {
            let o = {
              value: element.countPerson,
              name: this.$options.filters.getBoxType(element.personType)
            }
            return o
          })
        })
        .catch(error => {
          error && this.$message.error(error)
        })
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../styles/FirstTrial/fristtrial.less';
.ReportForm {
  .padding20 {
    padding: 20px;
  }
  .headertitle {
    align-items: center;
    justify-content: space-between;
  }
  .nodatapie {
    color: #909399;
    text-align: center;
    margin-top: 100px;
  }
  .countlist {
    .countlist-item {
      width: 300px;
      height: 118px;
      box-shadow: 0px 3px 10px 0px rgba(12, 52, 104, 0.15);
      border-radius: 6px;
      justify-content: center;
      flex-direction: column;
      padding: 0 30px;
      margin-right: 20px;
      background: url(../../assets/img/topechartback1.png);
      cursor: pointer;
      .itemtitle {
        font-size: 14px;
        color: #fff;
      }
      .itemcount {
        font-size: 36px;
        font-weight: 700;
        color: #fff;
      }
    }
    .countlist-item:last-child {
      margin-right: 20px;
    }
  }
  .towtitle {
    font-size: 16px;
    color: #333333;
    margin: 18px 0;
  }
  .backechartconcent {
    background-color: #ffffff;
    box-shadow: 0px 3px 10px 0px rgba(9, 143, 122, 0.15);
    border-radius: 10px;
    padding: 20px;
  }
  .echartconcent {
    flex-direction: row;
    height: 370px;
    .echartbintitle {
      font-size: 14px;
      position: relative;
      padding-left: 10px;
    }
    .echartbintitle::after {
      content: '';
      width: 4px;
      height: 12px;
      background-color: #00d0a6;
      border-radius: 2px;
      display: inline-block;
      position: absolute;
      left: 0;
      top: 4px;
    }
    .leftechartbinmain {
      width: 50%;
      box-shadow: 0px 3px 10px 0px rgba(9, 143, 122, 0.15);
      border-radius: 10px;
      margin-right: 30px;
      padding: 20px;
    }
    .rightchartbinmain {
      width: 50%;
      box-shadow: 0px 3px 10px 0px rgba(9, 143, 122, 0.15);
      border-radius: 10px;
      padding: 20px;
    }
  }
  /deep/ .el-carousel__indicator--horizontal {
    display: inline-block;
    padding: 4px 4px;
  }
  /deep/ .el-carousel__button {
    border-radius: 50%;
    width: 10px;
    height: 10px;
  }
}
</style>
