<template>
  <div>
    <slot name="before"></slot>
    <div :id="elId"
      :style="style"></div>
    <slot name="fater"></slot>
  </div>
</template>

<script>
// 引入基本模板
import echarts from 'echarts/lib/echarts'
import 'echarts/lib/chart/pie'
// 引入提示框组件、标题组件、工具箱组件。
import 'echarts/lib/component/legend'
import 'echarts/lib/component/title'
import 'echarts/lib/component/toolbox'

//引入uuid文件
import uuidv1 from 'uuid/v1'
export default {
  name: 'chart-pie2',
  props: {
    width: { type: String, default: '100%' },
    height: { type: String, default: '250px' },
    color: {
      type: [String, Object, Array],
      default() {
        return ['#f9d900', '#0ab3b0', '#06ccb1', '#00e0c1', '#fbbd02']
      }
    },
    pieData: {
      type: Array,
      default() {
        return []
      }
    },
    pieTitle: {
      type: String,
      default: '复查人员类型占比'
    }
  },
  data() {
    return {
      elId: '',
      option: {}
    }
  },
  created() {
    this.elId = uuidv1() //获取随机id
  },
  computed: {
    style() {
      return {
        height: this.height,
        width: this.width
      }
    }
  },
  watch: {
    pieData: {
      handler(val) {
        this.option.series.data = this.pieData
        this.chartObj.setOption(this.option, true)
      },
      deep: true
    }
  },
  mounted() {
    const getLegendDate = () => {
      let pieData = this.pieData
      return pieData.map(item => {
        return item.name
      })
    }
    const legendDate = getLegendDate()
    // console.log("legendDate", );
    const mytextStyle = {
      color: '#466791', //文字颜色
      fontStyle: 'normal', //italic斜体  oblique倾斜
      fontWeight: 'normal', //文字粗细bold   bolder   lighter  100 | 200 | 300 | 400...
      fontFamily: 'MicrosoftYaHei', //字体系列
      fontSize: 14 //字体大小
    }

    const mytitleStyle = {
      color: '#5588fb', //文字颜色
      fontWeight: 'bold', //文字粗细bold   bolder   lighter  100 | 200 | 300 | 400...
      fontFamily: 'MicrosoftYaHei', //字体系列
      fontSize: 16 //字体大小
    }

    const itemStyle = {
      normal: {
        barBorderRadius: 10,
        color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
          { offset: 0, color: '#4c9aff' },
          { offset: 1, color: '#7ab4ff' }
        ])
      }
    }
    const itemStyle1 = {
      normal: {
        color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
          { offset: 0, color: '#eaeaea' },
          { offset: 1, color: '#eaeaea' }
        ])
      }
    }
    // this.pieData[0].itemStyle = itemStyle;
    // this.pieData[0].itemStyle.normal.color = this.color;
    // this.pieData[1].itemStyle = itemStyle1;
    this.option = {
      tooltip: {
        trigger: 'item',
        formatter: '{a} <br/>{b}: {c} ({d}%)'
      },
      legend: {
        orient: 'horizontal',
        x: 'left',
        width: 200,
        itemWidth: 10,
        itemHeight: 10,
        left: 'auto',
        top: 10,
        right: 10,
        bottom: 'center',
        textStyle: mytextStyle,
        data: legendDate
      },
      series: [
        {
          name: this.pieTitle,
          color: this.color,
          type: 'pie',
          radius: [20, 100],
          center: ['35%', '50%'],
          roseType: 'area',
          startAngle: 0,
          avoidLabelOverlap: false,
          hoverOffset: 20,
          label: {
            show: true,
            formatter: '{d}%',
            color: '#7F98B7'
          },
          labelLine: {
            show: true,
            lineStyle: {
              color: '#7F98B7'
            }
          },
          data: this.pieData
        }
      ]
    }
    this.chartObj = echarts.init(document.getElementById(this.elId))
    this.chartObj.setOption(this.option, true)
  }
}
</script>
<style scoped></style>
